* {
  box-sizing: border-box;
}

.Frame {
  display: grid;
  grid-template-columns: auto auto auto;
  /* grid-template-columns: repeat(auto-fit, min-width(250px, 1fr)); */
  row-gap: 60px;
  column-gap: 120px;

  position: relative;
  width: 100px;
  height: 100px;
  margin-left: 40px;
  margin-top: 74px;
}
@media only screen and (max-width: 530px) {
  .Frame {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    margin-top: 20px;

    flex-direction: row;
    flex-flow: row wrap;
  }
}
@media only screen and (max-width: 1024px) {
  .Frame {
    grid-template-columns: auto auto;
    row-gap: 40px;

    column-gap: 80px;
  }
}
